import 'isomorphic-fetch'
import Vue from 'vue'
import Router from 'vue-router'
import SyncStore from './utils/sync-store'

Vue.use(SyncStore)
Vue.use(Router)

const router = new Router({
  mode: 'history',
  routes: [{
    path: '/',
    component: resolve => import('./components/Home').then(resolve)
  }]
})

export default { router }



